<template>
  <div class="wscn-http404">
    <div class="pic-404"></div>
    <div class="bullshit">
      <!-- <div class="bullshit__info">版权所有<a class="link-type" href="https://wallstreetcn.com" target='_blank'>华尔街见闻</a></div> -->
      <div class="bullshit__headline">{{ message }}</div>
      <div class="bullshit__info">
        请检查您输入的网址是否正确，请点击以下按钮返回主页
      </div>
      <a href="/" class="bullshit__return-home">返回首页</a>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  computed: {
    message() {
      return "对不起，您所访问的页面不存在或者已删除";
    }
  }
};
</script>

<style scoped>
.wscn-http404 {
  position: fixed;
  width: 100%;
  height: 100%;
}
.pic-404 {
  width: 65%;
  height: 65%;
  background: url("../../static/image/404bg.png");
  background-size: 100% 100%;
}

.bullshit {
  float: right;
  width: 48%;
  height: 35%;
  overflow: hidden;
}
.bullshit__headline {
  font-size: 25px;
  line-height: 24px;
  color: #1482f0;
  opacity: 0;
  margin-top: 60px !important;
  margin-bottom: 30px !important;
  animation-name: slideUp;
  animation-duration: 0.5s;
  animation-delay: 0.1s;
  animation-fill-mode: forwards;
}
.bullshit__info {
  font-size: 21px;
  line-height: 21px;
  color: grey;
  opacity: 0;
  margin-bottom: 30px !important;
  animation-name: slideUp;
  animation-duration: 0.5s;
  animation-delay: 0.2s;
  animation-fill-mode: forwards;
}
.bullshit__return-home {
  display: block;
  float: left;
  width: 110px;
  height: 36px;
  background: #1482f0;
  border-radius: 100px;
  text-align: center;
  color: #ffffff;
  opacity: 0;
  font-size: 14px;
  line-height: 36px;
  cursor: pointer;
  animation-name: slideUp;
  animation-duration: 0.5s;
  animation-delay: 0.3s;
  animation-fill-mode: forwards;
}
@keyframes slideUp {
  0% {
    transform: translateY(60px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
</style>



